























































import {
	childMixin
} from "../../../common/mixins.js";
export default {
	data() {
		return {
			agreestatus: false,
			baseurl: 'https://www.icoo.tech',
			activeStatus: 0,
			unlockNum: 0,
			challenge_list: [],
			banner_list: [],
			news_list: []
		}
	},
	mixins: [childMixin],
	onPullDownRefresh: function() {
		console.log('onPullDownRefresh');
		this.initDiscovery();
		this.initData(2);
		this.initData(3);
		this.initData(4);
	},
	onLoad() {
		this.initData(2);
		this.initData(3);
		this.initData(4);
	},
	onShow: function() {
		this.initDiscovery();
	},
	methods: {
		initData(type) {
			let that = this;
			this.$api.startBanner({
				type: type
			}, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					that.setBannerList(res.data.data, type);
				}
				uni.stopPullDownRefresh();
			}, function(err) {
				uni.stopPullDownRefresh();
				console.log(err)
			});
		},
		initDiscovery() {
			let that = this;
			this.$api.discoveryPage({
				userCode: this.$store.state.userCode,
				token: this.$store.state.token
			}, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					that.unlockNum = res.data.data.unlockNum;
					that.activeStatus = res.data.data.activeStatus;
				}
				if (res.data.status == 99) {
					that.loginOut(res.data.msg);
				}
				uni.stopPullDownRefresh();
			}, function(err) {
				uni.stopPullDownRefresh();
				console.log(err)
			});
		},
		target(tag) {
			if (tag == '') {
				return;
			}
			uni.navigateTo({
				url: '../../template/' + tag + '/' + tag
			});
		},
		setBannerList(value, type) {
			if (Array.isArray(value)) {
				if (type == 2) {
					this.news_list = value;
				}
				if (type == 3) {
					this.banner_list = value;
				}
				if (type == 4) {
					this.challenge_list = value;
				}
			}
		},
		challengeFn() {
			if (this.activeStatus == -1) {
				this.agreestatus = true;
			} else {
				uni.navigateTo({
					url: '../../template/challenge/challenge'
				});
			}
		},
		agreeFn() {
			this.agreestatus = false;
			if (this.unlockNum = 0) {
				uni.showToast({
					title: '连续签到7天解锁活动',
				});
			}
			if (this.activeStatus > -1) {
				uni.navigateTo({
					url: '../../template/challenge/challenge'
				});
			} else {
				uni.navigateTo({
					url: '../../template/challengeTarget/challengeTarget'
				});
			}
		},
		close() {
			this.agreestatus = false;
		},
		openViews(urltag) {
			this.$store.commit('set_homeNewsTarget', urltag);
			uni.navigateTo({
				url: '../../template/webView/webView',
			});
		},
	}
}



